.App {
  display: flex;
  flex-direction: column;
}

.App .Content {
  width: 100%;
  height: 100vh;
  background-image: url("./image/background/116387.jpg");
}

.App .Content {
  display: flex;
  flex-direction: column;
}

.App .Content h1 {
  color: #fff;
  text-shadow: black 0.1em 0.1em 0.2em;
  text-align: left;
  margin-top: 20vh;
  margin-left: 10vh;
  font-size: 5rem;
}

.App .Content p {
  color: var(--color-text-base);
  text-align: center;
  font-size: 40px;
  margin-top: 5;
  margin-bottom: 0;
}

.App .Content div {
  display: flex;
  flex-direction: column;
}

.App .Content div > a {
  width: 40rem;
  height: 7.6rem;
  background: var(--color-secondary);
  color: var(--color-button-text);
  border: 0;
  border-radius: 4rem;
  text-decoration: none;
  cursor: pointer;
  font: 700 2.5rem Archivo;
  display: flex;
  align-items: center;
  justify-content: space-evenly;

  transition: 0.2s;
  margin-top: 20vh;
  margin-left: 10vh;
}

.App .Content div > a:hover {
  background: var(--color-secondary-dark);
}

@media (max-width: 700px) {
  .App .Content {
    height: 40vh;
  }

  .App .Content div > a {
    width: 23rem;
    height: 4.6rem;
    border-radius: 4rem;
    font: 700 1.5rem Archivo;
  }

  .App .Content h1 {
    font-size: 40px;
  }

  .App .Content p {
    font-size: 20px;
  }
}
